<template>
  <div class="partner-box flex-col p-8 rounded-lg hover:shadow-2xl shadow-lg">
    <div>
      <div
        class="object-contain bg-rose-300 flex flex-col justify-center h-28 2xl:h-44 xl:h-36"
      >
        <img
          class="border-bt mt-3 object-scale-down h-32 max-w-1/2"
          :class="{
            'w-1/2': imageType === 'square',
            'w-full': imageType === 'rectangle',
          }"
          :src="getSrc(image)"
        />
      </div>
      <div class="pt-6 text-left">
        <span
          class="theme-color block text-xl font-semibold"
          v-html="title"
        ></span>
        <span
          class="block"
          v-html="'<small>' + description + '</small>'"
        ></span>
        <br />
        <a :href="link" class="website no-underline hover:underline" target="_blank">Website >></a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    index: {
      type: [Number],
      default: 0,
    },
    title: {
      type: [String],
      default: "",
    },
    description: {
      type: [String],
      default: "description",
    },
    link: {
      type: [String],
      default: "#",
    },
    image: {
      type: [String],
      default: "image",
    },
    imageType: {
      type: [String],
      default: "square",
    },
  },
  methods: {
    onClick(ref) {
      this.$refs[ref].scrollIntoView({
        behavior: "smooth",
        block: "start",
        inline: "start",
      });
    },
    getSrc(filename) {
      return "https://assets.bluca.io/images/team/" + filename
    },
  },
};
</script>

